import React, { useState, useEffect, useRef } from 'react';
import { 
  ShoppingCart, X, CheckCircle2, MessageCircle, Send, 
  MapPin, Loader2, Package, Truck, Home, Search, Plus, 
  ExternalLink, Minus 
} from 'lucide-react';
import { initializeApp } from "firebase/app";
import { 
  getDatabase, ref, onValue, push, set, serverTimestamp, 
  query, orderByChild, equalTo, runTransaction 
} from "firebase/database";
import { motion, AnimatePresence } from "framer-motion";

// ---  ---
const TG_BOT_LINK = "https://t.me/privatzzbot";
const DARKSTORE_COORDS = [55.8991, 37.7190]; 
const firebaseConfig = {
  databaseURL: "https://samokat-test-default-rtdb.europe-west1.firebasedatabase.app"
};

const app = initializeApp(firebaseConfig);
const db = getDatabase(app);

const getDistance = (lat1, lon1, lat2, lon2) => {
  const R = 6371;
  const dLat = (lat2 - lat1) * Math.PI / 180;
  const dLon = (lon2 - lon1) * Math.PI / 180;
  const a = Math.sin(dLat/2) * Math.sin(dLat/2) +
            Math.cos(lat1 * Math.PI / 180) * Math.cos(lat2 * Math.PI / 180) * Math.sin(dLon/2) * Math.sin(dLon/2);
  return R * 2 * Math.atan2(Math.sqrt(a), Math.sqrt(1-a));
};

//   
const SuccessAnimation = ({ onFinished }) => (
  <motion.div 
    initial={{ opacity: 0 }} animate={{ opacity: 1 }} exit={{ opacity: 0 }}
    className="fixed inset-0 z-[5000] bg-white flex flex-col items-center justify-center p-6 text-center"
  >
    <div className="relative mb-6">
      <motion.div
        initial={{ scale: 0 }} animate={{ scale: 1 }}
        transition={{ type: "spring", damping: 12, stiffness: 200, delay: 0.2 }}
        className="w-24 h-24 bg-green-500 rounded-full flex items-center justify-center shadow-lg shadow-green-200"
      >
        <motion.svg 
          width="50" height="50" viewBox="0 0 24 24" fill="none" 
          stroke="white" strokeWidth="4" strokeLinecap="round" strokeLinejoin="round"
        >
          <motion.path 
            d="M20 6L9 17L4 12" 
            initial={{ pathLength: 0 }} animate={{ pathLength: 1 }}
            transition={{ duration: 0.5, delay: 0.5 }}
          />
        </motion.svg>
      </motion.div>
    </div>
    <motion.h2 
      initial={{ y: 20, opacity: 0 }} animate={{ y: 0, opacity: 1 }} transition={{ delay: 0.7 }}
      className="text-3xl font-black italic text-[#FF335F] mb-2"
    >
       !
    </motion.h2>
    <motion.p 
      initial={{ y: 20, opacity: 0 }} animate={{ y: 0, opacity: 1 }} transition={{ delay: 0.9 }}
      className="text-gray-500 font-bold mb-8"
    >
           
    </motion.p>
    <motion.button
      whileTap={{ scale: 0.95 }}
      onClick={onFinished}
      className="bg-[#FF335F] text-white px-10 py-4 rounded-2xl font-black shadow-lg"
    >
      
    </motion.button>
  </motion.div>
);

const SamokatApp = () => {
  const [products, setProducts] = useState([]);
  const [cart, setCart] = useState([]);
  const [searchQuery, setSearchQuery] = useState("");
  const [selectedCategory, setSelectedCategory] = useState(' ');
  const [isCartOpen, setIsCartOpen] = useState(false);
  const [isChatOpen, setIsChatOpen] = useState(false);
  const [isMapOpen, setIsMapOpen] = useState(false);
  const [showAddressDetails, setShowAddressDetails] = useState(false);
  const [orderStatus, setOrderStatus] = useState('idle'); 
  const [address, setAddress] = useState('');
  const [deliveryTime, setDeliveryTime] = useState(15);
  const [addressDetails, setAddressDetails] = useState({ entrance: '', floor: '', apartment: '', intercom: '' });
  const [mapPos, setMapPos] = useState([55.751244, 37.618423]);
  const [userIP, setUserIP] = useState('');
  const [activeOrder, setActiveOrder] = useState(null);
  const [showSuccess, setShowSuccess] = useState(false);

  const dgMap = useRef(null);

  const [messages, setMessages] = useState([]);
  const [newMessage, setNewMessage] = useState("");
  const [chatId] = useState(() => {
    const saved = localStorage.getItem('chatId');
    if (saved) return saved;
    const newId = "chat_" + Math.random().toString(36).substr(2, 9);
    localStorage.setItem('chatId', newId);
    return newId;
  });

  const categories = [' ', '', '', '', '', ''];
  const statusLabels = {
    pending: " ",
    confirmed: " ",
    assembling: " ",
    delivering: "  ",
    completed: "",
    canceled: ""
  };

  useEffect(() => {
    fetch('https://api.ipify.org?format=json')
      .then(r => r.json())
      .then(d => {
        setUserIP(d.ip);
        const q = query(ref(db, 'orders'), orderByChild('userIP'), equalTo(d.ip));
        onValue(q, (snapshot) => {
          const data = snapshot.val();
          if (data) {
            const list = Object.values(data);
            const last = list[list.length - 1];
            if (last.status !== 'completed' && last.status !== 'canceled') {
              setActiveOrder(last);
            } else { setActiveOrder(null); }
          }
        });
      });

    onValue(ref(db, 'products'), (snapshot) => {
      const data = snapshot.val();
      if (data) setProducts(Object.keys(data).map(k => ({ id: k, ...data[k] })));
    });

    onValue(ref(db, `support_chats/${chatId}/messages`), (s) => {
      if (s.exists()) setMessages(Object.values(s.val()));
    });
  }, [chatId]);

  const fetchAddress = async (lat, lng) => {
    try {
      const res = await fetch(`https://nominatim.openstreetmap.org/reverse?format=json&lat=${lat}&lon=${lng}&addressdetails=1`);
      const data = await res.json();
      const addr = data.address;
      return `${addr.road || ''}, ${addr.house_number || ''}`.replace(/, $/, "");
    } catch (e) {
      return `: ${lat.toFixed(4)}, ${lng.toFixed(4)}`;
    }
  };

  useEffect(() => {
    if (isMapOpen && !dgMap.current) {
      setTimeout(() => {
        window.DG.then(() => {
          dgMap.current = window.DG.map('map-container', {
            center: mapPos,
            zoom: 15,
            zoomControl: false,
          });

          const marker = window.DG.marker(mapPos).addTo(dgMap.current);

          dgMap.current.on('click', async (e) => {
            const { lat, lng } = e.latlng;
            marker.setLatLng([lat, lng]);
            const dist = getDistance(lat, lng, DARKSTORE_COORDS[0], DARKSTORE_COORDS[1]);
            setDeliveryTime(dist > 2.5 ? 30 : 15);
            const friendlyAddress = await fetchAddress(lat, lng);
            setAddress(friendlyAddress);
            setMapPos([lat, lng]);
            setShowAddressDetails(true);
          });
        });
      }, 100);
    }
    return () => { if(dgMap.current) { dgMap.current.remove(); dgMap.current = null; } };
  }, [isMapOpen, mapPos]);

  const addToCart = (product) => {
    setCart(prev => {
      const existing = prev.find(item => item.id === product.id);
      if (existing) return prev.map(item => item.id === product.id ? {...item, qty: item.qty + 1} : item);
      return [...prev, {...product, qty: 1}];
    });
  };

  const updateQty = (id, delta) => {
    setCart(prev => prev.map(item => (item.id === id) ? {...item, qty: Math.max(1, item.qty + delta)} : item));
  };

  const sendMessage = async (text = null) => {
    const messageText = text || newMessage;
    if (!messageText.trim()) return;
    await push(ref(db, `support_chats/${chatId}/messages`), { text: messageText, sender: "user", timestamp: serverTimestamp() });
    await set(ref(db, `support_chats/${chatId}/meta`), { lastMessage: messageText, timestamp: serverTimestamp(), unread: true, chatId });
    setNewMessage("");
  };

  const handleCheckout = async () => {
    if (!address) return alert(" !");
    setOrderStatus('processing');
    try {
      for (const item of cart) {
        await runTransaction(ref(db, `products/${item.id}/stock`), (stock) => {
          if (stock < item.qty) throw new Error(`  : ${item.name}`);
          return stock - item.qty;
        });
      }
      const orderRef = push(ref(db, 'orders'));
      await set(ref(db, `orders/${orderRef.key}`), {
        id: orderRef.key, items: cart, total: cart.reduce((s, i) => s + (i.price * i.qty), 0),
        address: `${address}, .${addressDetails.apartment}, .${addressDetails.floor}`,
        userIP, status: 'pending', createdAt: serverTimestamp()
      });
      setCart([]); 
      setIsMapOpen(false); 
      setShowSuccess(true);
    } catch (e) { alert(e.message); setOrderStatus('idle'); }
  };

  const finishOrder = () => {
    setShowSuccess(false);
    setOrderStatus('tracking');
    setIsCartOpen(true);
  };

  const filteredProducts = products.filter(p => 
    (selectedCategory === ' ' || p.cat === selectedCategory) &&
    p.name.toLowerCase().includes(searchQuery.toLowerCase())
  );

  return (
    <div className="min-h-screen bg-[#F6F6F6] text-[#2B2B2B]">
      <AnimatePresence>
        {showSuccess && <SuccessAnimation onFinished={finishOrder} />}
      </AnimatePresence>

      {/* HEADER */}
      <header className="sticky top-0 z-[1000] bg-white h-20 px-4 md:px-8 border-b flex items-center justify-between shadow-sm">
        <div className="flex items-center gap-4">
          <h1 className="text-2xl md:text-3xl font-black text-[#FF335F] italic tracking-tighter cursor-pointer" onClick={() => window.location.reload()}></h1>
          {activeOrder && (
            <button onClick={() => {setOrderStatus('tracking'); setIsCartOpen(true);}} className="bg-pink-50 text-[#FF335F] px-3 py-1.5 rounded-xl font-bold text-sm flex items-center gap-2 border border-pink-100 animate-pulse">
              <Package size={16}/> <span>  </span>
            </button>
          )}
        </div>
        <div className="flex-1 max-w-md mx-4 hidden md:block relative">
           <Search className="absolute left-3 top-1/2 -translate-y-1/2 text-gray-400" size={16}/>
           <input className="w-full bg-gray-100 py-2.5 pl-10 pr-4 rounded-xl outline-none" placeholder="  ..." value={searchQuery} onChange={e=>setSearchQuery(e.target.value)} />
        </div>
        <div className="flex items-center gap-2">
          <button onClick={() => setIsChatOpen(true)} className="p-3 bg-gray-100 rounded-full relative">
            <MessageCircle size={20}/><span className="absolute top-0 right-0 w-3 h-3 bg-pink-500 border-2 border-white rounded-full"></span>
          </button>
          <button onClick={() => setIsCartOpen(true)} className="bg-[#FF335F] text-white px-5 py-3 rounded-2xl font-black flex items-center gap-2">
            <ShoppingCart size={18}/> {cart.reduce((s,i)=>s+(i.price*i.qty),0)} 
          </button>
        </div>
      </header>

      <main className="max-w-[1600px] mx-auto p-4 md:p-8 flex gap-8">
        <aside className="hidden lg:flex flex-col gap-4 w-64 shrink-0">
           <div className="bg-white p-6 rounded-[32px] border border-pink-100 shadow-sm">
              <p className="text-[10px] font-black uppercase text-pink-500 mb-2"> </p>
              <h3 className="font-black text-xl mb-1 text-[#FF335F]"> 200</h3>
              <p className="text-xs text-gray-500 font-bold">    800   <span className="text-black">FIRST</span></p>
           </div>
           <div className="bg-[#2B2B2B] p-6 rounded-[32px] text-white relative overflow-hidden group">
              <div className="relative z-10">
                <p className="font-black italic text-lg tracking-tighter"> + </p>
                <p className="text-[10px] opacity-60 mt-2 font-bold uppercase">     5%   </p>
              </div>
              <Send className="absolute -bottom-4 -right-4 text-white/10 group-hover:scale-110 transition-transform" size={100}/>
           </div>
        </aside>

        <div className="flex-1">
          <div className="mb-8 bg-gradient-to-r from-[#FF335F] to-[#FF5E81] rounded-[32px] p-6 md:p-10 text-white flex flex-col md:flex-row justify-between items-center shadow-xl relative overflow-hidden">
             <div className="relative z-10 text-center md:text-left">
                <h2 className="text-3xl md:text-4xl font-black mb-2 tracking-tighter"> Telegram-</h2>
                <p className="text-white/80 font-bold max-w-sm">     !</p>
             </div>
             <a href={TG_BOT_LINK} className="relative z-10 bg-white text-[#FF335F] px-8 py-4 rounded-2xl font-black mt-4 md:mt-0 flex items-center gap-2">
                 <ExternalLink size={20}/>
             </a>
          </div>

          <div className="flex gap-2 overflow-x-auto pb-6">
            {categories.map(c => (
              <button key={c} onClick={()=>setSelectedCategory(c)} className={`px-5 py-2.5 rounded-xl font-bold whitespace-nowrap transition-all ${selectedCategory===c ? 'bg-[#FF335F] text-white' : 'bg-white'}`}>{c}</button>
            ))}
          </div>

          <div className="grid grid-cols-2 md:grid-cols-3 xl:grid-cols-4 gap-4">
            {filteredProducts.map(p => (
              <div key={p.id} className="bg-white p-4 rounded-[32px] flex flex-col justify-between hover:shadow-lg transition-all border border-transparent hover:border-pink-50">
                <div>
                  <div className="aspect-square mb-3 bg-gray-50 rounded-2xl flex items-center justify-center p-4 relative">
                    <img src={p.img} alt={p.name} className="object-contain max-h-full" />
                    {(!p.stock || p.stock <= 0) && <div className="absolute inset-0 bg-white/80 flex items-center justify-center font-black text-gray-400 uppercase text-xs">  </div>}
                  </div>
                  <div className="flex justify-between items-start mb-1">
                    <span className="font-black text-xl">{p.price} </span>
                    <span className={`text-[10px] px-2 py-1 rounded-lg font-bold ${p.stock < 5 ? 'bg-red-50 text-red-500' : 'bg-green-50 text-green-600'}`}>{p.stock} </span>
                  </div>
                  <p className="font-bold text-sm text-gray-600 line-clamp-2">{p.name}</p>
                </div>
                <button disabled={!p.stock || p.stock <= 0} onClick={() => addToCart(p)} className="w-full bg-gray-100 py-3 rounded-2xl mt-4 font-black text-sm hover:bg-[#FF335F] hover:text-white transition">+ </button>
              </div>
            ))}
          </div>
        </div>

        <aside className="hidden xl:flex flex-col gap-4 w-64 shrink-0">
           <div className="bg-gradient-to-br from-yellow-400 to-orange-500 p-6 rounded-[32px] text-white shadow-lg">
              <h3 className="font-black text-lg leading-tight"> </h3>
              <p className="text-xs font-bold mt-2 opacity-90">      1530    .</p>
           </div>
           <div className="bg-white p-6 rounded-[32px] border border-gray-100 shadow-sm">
              <h4 className="font-black text-sm uppercase tracking-tighter text-gray-400 mb-3"></h4>
              <button onClick={()=>setIsChatOpen(true)} className="w-full bg-gray-50 py-3 rounded-xl font-bold text-sm flex items-center justify-center gap-2 hover:bg-gray-100">
                <MessageCircle size={16}/>  
              </button>
           </div>
        </aside>
      </main>

      {isMapOpen && (
        <div className="fixed inset-0 z-[3000] bg-black/60 backdrop-blur-sm flex items-center justify-center p-0 md:p-4">
          <div className="bg-white w-full max-w-4xl h-full md:h-[90vh] md:rounded-[40px] overflow-hidden flex flex-col shadow-2xl relative">
            <button onClick={() => setIsMapOpen(false)} className="absolute top-6 right-6 z-[5000] bg-white p-3 rounded-full shadow-xl hover:rotate-90 transition-all"><X/></button>
            <div className="flex-1 w-full relative bg-gray-100">
               <div id="map-container" style={{ width: '100%', height: '100%' }}></div>
               <div className="absolute top-1/2 left-1/2 -translate-x-1/2 -translate-y-[70px] z-[4000] pointer-events-none flex flex-col items-center">
                  <div className="bg-[#FF335F] text-white px-4 py-2 rounded-2xl font-black shadow-xl flex items-center gap-2 animate-bounce">
                    ~{deliveryTime} 
                  </div>
                  <div className="w-0 h-0 border-l-[8px] border-l-transparent border-r-[8px] border-r-transparent border-t-[10px] border-t-[#FF335F]"></div>
               </div>
            </div>
            <div className="bg-white p-6 md:p-8 shadow-[0_-10px_40px_rgba(0,0,0,0.1)] z-10">
               <div className="max-w-2xl mx-auto text-center md:text-left">
                 <div className="flex items-center justify-center md:justify-start gap-2 text-gray-400 mb-2">
                    <MapPin size={16} className="text-[#FF335F]"/>
                    <span className="text-xs font-black uppercase tracking-widest"> </span>
                 </div>
                 <p className="font-black text-2xl mb-6">{address || "  ..."}</p>
                 {showAddressDetails && (
                   <div className="grid grid-cols-2 md:grid-cols-4 gap-4 mb-8">
                     <input placeholder="" className="w-full bg-gray-50 p-4 rounded-2xl font-bold" onChange={e=>setAddressDetails({...addressDetails, apartment: e.target.value})}/>
                     <input placeholder="" className="w-full bg-gray-50 p-4 rounded-2xl font-bold" onChange={e=>setAddressDetails({...addressDetails, entrance: e.target.value})}/>
                     <input placeholder="" className="w-full bg-gray-50 p-4 rounded-2xl font-bold" onChange={e=>setAddressDetails({...addressDetails, floor: e.target.value})}/>
                     <input placeholder="" className="w-full bg-gray-50 p-4 rounded-2xl font-bold" onChange={e=>setAddressDetails({...addressDetails, intercom: e.target.value})}/>
                   </div>
                 )}
                 <button onClick={handleCheckout} disabled={!address || orderStatus === 'processing'} className="w-full bg-[#FF335F] text-white py-5 rounded-[24px] font-black text-xl shadow-lg shadow-pink-200">
                   {orderStatus === 'processing' ? <Loader2 className="animate-spin mx-auto"/> : '  '}
                 </button>
               </div>
            </div>
          </div>
        </div>
      )}

      {isCartOpen && (
        <div className="fixed inset-0 z-[2000] bg-black/40 backdrop-blur-sm flex justify-end">
          <div className="w-full max-w-md bg-white h-full p-8 flex flex-col shadow-2xl animate-in slide-in-from-right duration-300">
             <div className="flex justify-between items-center mb-8">
                <h2 className="text-3xl font-black italic text-[#FF335F] tracking-tighter">{orderStatus === 'tracking' ? '' : ''}</h2>
                <button onClick={() => setIsCartOpen(false)} className="p-2 bg-gray-100 rounded-full"><X/></button>
             </div>
             {orderStatus === 'tracking' && activeOrder ? (
               <div className="flex-1 overflow-y-auto pr-2">
                 <div className="bg-gray-50 p-5 rounded-[24px] mb-8 border border-gray-100 relative">
                    <p className="text-[10px] text-gray-400 font-black mb-1 tracking-widest uppercase"> </p>
                    <p className="font-bold text-sm">{activeOrder.address}</p>
                 </div>
                 <div className="space-y-8 relative pl-6">
                    <div className="absolute left-[31px] top-4 bottom-4 w-[2px] bg-gray-100"></div>
                    {['pending','confirmed','assembling','delivering','completed'].map((s,i) => {
                      const stages = ['pending','confirmed','assembling','delivering','completed'];
                      const isDone = i <= stages.indexOf(activeOrder.status);
                      return (
                        <div key={s} className="flex gap-6 relative items-center">
                          <div className={`w-12 h-12 rounded-2xl z-10 flex items-center justify-center ${isDone ? 'bg-[#FF335F] text-white shadow-lg' : 'bg-gray-100 text-gray-300'}`}>
                             {i===0 && <Loader2 className={activeOrder.status==='pending'?'animate-spin':''}/>}
                             {i===1 && <CheckCircle2/>}
                             {i===2 && <Package/>}
                             {i===3 && <Truck/>}
                             {i===4 && <Home/>}
                          </div>
                          <div>
                            <p className={`font-black uppercase text-xs tracking-widest ${isDone?'text-[#2B2B2B]':'text-gray-300'}`}>{statusLabels[s]}</p>
                            {activeOrder.status === s && <p className="text-[10px] text-[#FF335F] font-bold animate-pulse"> </p>}
                          </div>
                        </div>
                      )
                    })}
                 </div>
               </div>
             ) : (
               <>
                 <div className="flex-1 overflow-y-auto space-y-4 pr-2">
                    {cart.length === 0 ? (
                      <div className="text-center py-20">
                        <div className="bg-gray-50 w-20 h-20 rounded-full flex items-center justify-center mx-auto mb-4 text-gray-300"><ShoppingCart size={32}/></div>
                        <p className="font-bold text-gray-400">   </p>
                      </div>
                    ) : (
                      cart.map(item => (
                        <div key={item.id} className="flex items-center gap-4 bg-gray-50 p-4 rounded-3xl">
                           <img src={item.img} className="w-16 h-16 object-contain" alt=""/>
                           <div className="flex-1">
                              <p className="font-bold text-sm leading-tight">{item.name}</p>
                              <div className="flex items-center gap-3 mt-2">
                                 <button onClick={()=>updateQty(item.id, -1)} className="w-8 h-8 bg-white rounded-xl flex items-center justify-center"><Minus size={14}/></button>
                                 <span className="font-black text-sm">{item.qty}</span>
                                 <button onClick={()=>updateQty(item.id, 1)} className="w-8 h-8 bg-white rounded-xl flex items-center justify-center"><Plus size={14}/></button>
                              </div>
                           </div>
                           <div className="text-right">
                              <p className="font-black text-sm">{item.price * item.qty} </p>
                           </div>
                        </div>
                      ))
                    )}
                 </div>
                 {cart.length > 0 && (
                   <div className="pt-6 border-t mt-6">
                      <div className="flex justify-between items-end mb-6">
                         <span className="text-gray-400 font-bold"></span>
                         <span className="text-3xl font-black">{cart.reduce((s,i)=>s+(i.price*i.qty),0)} </span>
                      </div>
                      <button onClick={()=>setIsMapOpen(true)} className="w-full bg-[#FF335F] text-white py-5 rounded-[24px] font-black text-xl shadow-lg"> </button>
                   </div>
                 )}
               </>
             )}
          </div>
        </div>
      )}

      {/*   */}
      {isChatOpen && (
        <div className="fixed inset-0 z-[4000] bg-black/40 backdrop-blur-sm flex items-end md:items-center justify-center p-0 md:p-4">
           <div className="bg-white w-full max-w-lg h-[80vh] md:h-[600px] md:rounded-[40px] shadow-2xl flex flex-col overflow-hidden animate-in slide-in-from-bottom duration-300">
              <div className="bg-[#FF335F] p-6 text-white flex justify-between items-center">
                 <div className="flex items-center gap-3">
                    <div className="w-10 h-10 bg-white/20 rounded-2xl flex items-center justify-center"><MessageCircle/></div>
                    <div><p className="font-black text-lg"></p><p className="text-[10px] font-bold opacity-70 uppercase tracking-widest">  2 </p></div>
                 </div>
                 <button onClick={()=>setIsChatOpen(false)} className="bg-white/10 p-2 rounded-full"><X/></button>
              </div>
              
              <div className="flex-1 overflow-y-auto p-6 space-y-4">
                 {/*     */}
                 <div className="flex justify-start">
                    <div className="max-w-[80%] p-4 rounded-3xl font-bold text-sm bg-gray-100 text-gray-700 rounded-bl-none">
                       !  ,    .     ,   :
                    </div>
                 </div>

                 {/*    */}
                 <div className="grid grid-cols-1 gap-2 mt-2">
                    {['  ', '   ', '   ', ' '].map(btn => (
                       <button 
                        key={btn}
                        onClick={() => sendMessage(btn)}
                        className="w-full bg-white border border-gray-200 py-3 rounded-2xl font-bold text-sm hover:bg-gray-50 transition-colors shadow-sm"
                       >
                          {btn}
                       </button>
                    ))}
                 </div>

                 {/*   */}
                 {messages.map((m, i) => (
                   <div key={i} className={`flex ${m.sender==='user'?'justify-end':'justify-start'}`}>
                      <div className={`max-w-[80%] p-4 rounded-3xl font-bold text-sm ${m.sender==='user'?'bg-[#FF335F] text-white rounded-br-none':'bg-gray-100 text-gray-700 rounded-bl-none'}`}>{m.text}</div>
                   </div>
                 ))}
              </div>

              <div className="p-4 bg-gray-50 border-t flex gap-2">
                 <input className="flex-1 bg-white p-4 rounded-2xl font-bold text-sm outline-none" placeholder=" ..." value={newMessage} onChange={e=>setNewMessage(e.target.value)} onKeyPress={e=>e.key==='Enter' && sendMessage()} />
                 <button onClick={() => sendMessage()} className="bg-[#FF335F] text-white p-4 rounded-2xl shadow-md"><Send size={20}/></button>
              </div>
           </div>
        </div>
      )}
    </div>
  );
};

export default SamokatApp;